import React from "react";
import { Route, Redirect } from "react-router-dom";
import { getUserToken } from "../../utils/session";
import { privatePath, publicPath } from "../../utils/route-constants";

export const PrivateRoute = ({ component: Component, ...rest }) => {
  return (
    <Route
      {...rest}
      render={props =>
        getUserToken() ? (
          <Component {...props} />
        ) : (
          <Redirect to={publicPath.login} />
        )
      }
    />
  );
};

export const PublicRoute = ({ component: Component, allowLogin, ...rest }) => {
  return (
    <Route
      {...rest}
      render={props =>
        getUserToken() && !allowLogin ? (
          <Redirect to={privatePath.dashboard} />
        ) : (
          <Component {...props} />
        )
      }
    />
  );
};
