import React from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";

const Wrapper = styled.div`
  border: 1px solid #ddd;
  padding: 15px;
  border-radius: 4px;
  margin-bottom: 10px;
  transition: 0.3s all ease-in-out;
  position: relative;
  text-align: center;
  :hover {
    box-shadow: 0 5px 10px 2px rgba(0, 0, 0, 0.1);
  }
  cursor: pointer;
`;

const StyledLink = styled(Link)`
  color: #333333;
  transition: all 0.2s;
  display: inline-block;
  width: 100%;
  :hover {
    text-decoration: none;
    color: #fff;
    img {
      filter: brightness(2) grayscale(1);
      z-index: 9;
    }
    :after {
      opacity: 1;
      transform: scale(1);
    }
  }
  :after {
    position: absolute;
    width: 100%;
    height: 100%;
    content: "";
    background: #17d0cf;
    background: -moz-linear-gradient(
      left,
      #01a6fd 0%,
      #17d0cf 51%,
      #01a6fd 100%
    );
    background: -webkit-gradient(
      left top,
      right top,
      color-stop(0%, #4eb3bf),
      color-stop(51%, #17d0cf),
      color-stop(100%, #4eb3bf)
    );
    background: -webkit-linear-gradient(
      left,
      #01a6fd 0%,
      #17d0cf 51%,
      #0071b0 100%
    );
    background: -o-linear-gradient(left, #01a6fd 0%, #17d0cf 51%, #01a6fd 100%);
    background: -ms-linear-gradient(
      left,
      #01a6fd 0%,
      #17d0cf 51%,
      #01a6fd 100%
    );
    background: -webkit-gradient(
      linear,
      left top,
      right top,
      from(#01a6fd),
      color-stop(51%, #17d0cf),
      to(#01a6fd)
    );
    background: -webkit-linear-gradient(
      left,
      #01a6fd 0%,
      #17d0cf 51%,
      #01a6fd 100%
    );
    background: linear-gradient(
      to right,
      #01a6fd 0%,
      #17d0cf 51%,
      #01a6fd 100%
    );
    background-size: auto;
    background-size: 200% auto;
    -webkit-transition: 0.3s all ease-in-out;
    -o-transition: 0.3s all ease-in-out;
    transition: 0.3s all ease-in-out;
    left: 0;
    top: 0;
    z-index: 0;
    opacity: 0;
    border-radius: 4px;
    transform: scale(0);
  }
`;

const Img = styled.img`
  width: 50px;
  margin-bottom: 10px;
`;

const H5 = styled.h5`
  margin: 10px 0 0;
  font-weight: 500;
  font-size: 18px;
  position: relative;
  z-index: 1;
`;

const PanelButton = ({ to, imgSrc, imgAlt = "", text }) => {
  return (
    <Wrapper>
      <StyledLink to={to}>
        <Img src={imgSrc} alt={imgAlt} />
        <H5>{text}</H5>
      </StyledLink>
    </Wrapper>
  );
};

export default PanelButton;
