import React from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import Button from "./button";
import withApiWrapper from "../../utils/withApiWrapper";
import { getUserId } from "../../utils/session";
import { privatePath } from "../../utils/route-constants";

import logo from "../../images/logo.png";
import avatar from "../../images/avatar.png";

const StyledImg = styled.img`
  width: 45px;
  border-style: none;
  cursor: pointer;
  @media (min-width: 992px) {
    position: absolute;
    left: 50%;
  }
`;

const Avatar = styled.img`
  margin: 0px 12px;
  width: 25px;
`;

const ButtonDiv = styled.div`
  display: flex;
  width: fit-content;
  @media (max-width: 991px) {
    ${props => props.mHidden && "display: none;"}
  }
`;

const DeskTopWrapper = styled.div`
  width: 100%;
  height: 42px;
  display: flex;
  justify-content: space-between;
`;

const MobileRowWrapper = styled.div`
  display: flex;
  justify-content: center;
  margin: 0 auto;
  margin-top: 15px;
  padding-top: 15px;
  width: calc(100%);
  align-self: center;
  border-top: 1px solid #ddd;
  @media (min-width: 992px) {
    display: none;
  }
`;

const UserDetailWrapper = styled.div`
right: 0px;
color: #333;
align-self: center;
font-family: 'Roboto'
  width: fit-content;
  display: flex;
  justify-content: space-between;
  @media (min-width: 992px) {
    margin-left: auto;
  }
`;

const StyledLink = styled(Link)`
  :hover {
    text-decoration: none;
  }
`;

const initState = {
  firstName: "",
  lastName: "",
  dateOfBirth: "",
  email: "",
  phoneNumber: "",
  sex: "",
  allowBonus: "",
  amount: "",
  bankAccountNumber: "",
  bankName: ""
};

const ButtonGroup = ({ mHidden }) => {
  return (
    <ButtonDiv mHidden={mHidden}>
      <StyledLink to={privatePath.dashboard}>
        <Button text="หน้าแรก" iconClass="fas fa-home" />
      </StyledLink>

      <Button
        text="ไปหน้าเล่นเกม"
        iconClass="fas fa-gamepad"
        marginLeft={10}
        invertColor
      />
      <Button text="โปรโมชั่น" iconClass="fas fa-percentage" marginLeft={10} />
    </ButtonDiv>
  );
};

const PrivateNavbar = ({ apiCall }) => {
  const [userInfo, setUserInfo] = React.useState({ ...initState });
  const fetchUserInfo = async () => {
    const res = await apiCall({
      method: "get",
      path: `/wallet-owner/${getUserId()}`
    });
    if (res) {
      setUserInfo(res);
    }
  };
  React.useEffect(() => {
    fetchUserInfo();
    return () => {
      setUserInfo({ ...initState });
    };
  }, []);
  const { email, amount } = userInfo;
  return (
    <>
      <DeskTopWrapper>
        <ButtonGroup mHidden={true} />
        <StyledImg src={logo} />
        <UserDetailWrapper>
          {`เครดิต : ${amount}`}
          <Avatar src={avatar} />
          {email}
        </UserDetailWrapper>
      </DeskTopWrapper>
      <MobileRowWrapper>
        <ButtonGroup />
      </MobileRowWrapper>
    </>
  );
};

export default withApiWrapper(PrivateNavbar);
