import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import { publicPath, privatePath } from "./utils/route-constants";
import { PublicRoute, PrivateRoute } from "./components/routes";
import Navbar from "./components/navbar";
import Footer from "./components/footer";
import Login from "./containers/login";
import Register from "./containers/register";
import NotFound from "./containers/not-found";
import Dashboard from "./containers/dashboard";
import ForgetPasswod from "./containers/forget-password";
import ResetPassword from "./containers/reset-password";
import Setting from "./containers/setting";

function App() {
  return (
    <Router>
      <Navbar />
      <Switch>
        <PublicRoute path={publicPath.login} exact component={Login} />
        <PublicRoute path={publicPath.register} exact component={Register} />
        <PublicRoute
          path={publicPath.forgetPassword}
          exact
          component={ForgetPasswod}
        />
        <PublicRoute
          path={publicPath.resetPassword}
          exact
          component={ResetPassword}
        />
        <PrivateRoute path={privatePath.setting} exact component={Setting} />
        <PrivateRoute
          path={privatePath.dashboard}
          exact
          component={Dashboard}
        />
        <Route component={NotFound} />
      </Switch>
      <Footer />
    </Router>
  );
}

export default App;
