import React from "react";
import styled, { css } from "styled-components";
import HashLoader from "react-spinners/HashLoader";

const Wrapper = styled.div`
  background-color: #fff;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.04);
  max-width: 720px;
  min-height: calc(
    100vh - ${props => (props.isPublicPage ? 73 : 140)}px - 80px - 100px
  );
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin: 40px auto;
  max-width: 720px;
  background-color: #fff;
  padding: 20px;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.04);
  flex: 1 1 0%;

  // @media (min-width: 576px) {
  //   max-width: 540px;
  // }
  @media (min-width: 768px) {
    max-width: 720px;
  }
  @media (min-width: 992px) {
    min-height: calc(100vh - 73px - 80px - 100px);
    max-width: 960px;
  }
  @media only screen and (min-width: 992px) {
    max-width: 720px;
  }
  @media only screen and (min-width: 992px) {
    max-width: 840px;
  }
`;

const TitleBar = styled.div`
  margin-bottom: 30px;
  width: 100%;
  position: relative;
  text-align: center;
`;

const loadingStyle = css`
  margin: 15px auto 0 auto;
`;

const H3 = styled.h3`
  margin: 0;
  color: #333;
  position: relative;
  display: inline-block;
  width: auto;
  font-weight: 600;
  :before,
  :after {
    content: ".";
    font-size: 70px;
    left: -45px;
    pointer-events: none;
    position: absolute;
    text-shadow: transparent 0 0;
    top: -1px;
    line-height: 0;
    color: #17d0cf;
    text-shadow: 15px 0 #17d0cf;
  }
  :after {
    right: -28px;
    left: inherit;
  }
`;

const PageWrapper = ({
  children,
  defaultTitle,
  isLoading = false,
  isError,
  errorText,
  isPublicPage,
  closeErr = () => {}
}) => {
  return (
    <Wrapper isPublicPage={isPublicPage}>
      <div className="row">
        <div className="col-12 mx-auto">
          {defaultTitle && (
            <TitleBar className="section-title default-title">
              <H3>{defaultTitle}</H3>
            </TitleBar>
          )}
          {isError && (
            <div
              className="alert alert-danger alert-dismissible fade show"
              role="alert">
              {errorText || "เกิดข้อผิดพลาดขึ้น ไม่สามารถใช้งานได้ในขณะนี้"}
              <button
                onClick={closeErr}
                type="button"
                className="close"
                data-dismiss="alert"
                aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
          )}
          {children}
        </div>
      </div>
      <HashLoader
        css={loadingStyle}
        size={50}
        color={"#01a6fd"}
        loading={isLoading}
      />
    </Wrapper>
  );
};

export default PageWrapper;
