import React from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import Wrapper from "../../components/page-wrapper";
import Btn from "../../components/button/";
import withApiWrapper from "../../utils/withApiWrapper";
import { setUserToken, setUserId } from "../../utils/session";
import { privatePath, publicPath } from "../../utils/route-constants";

const H3 = styled.h3`
  font-weight: 600;
  margin-bottom: 30px;
`;

const Form = styled.form`
  padding: 30px;
  display: block;
`;

const Input = styled.input`
  font-size: 14px;
  position: relative;
  z-index: 1;
  padding: 8px 10px;
  resize: none;
  border: 1px solid #ddd;
  border-radius: 0;
  background: none;
  box-shadow: none !important;
  font-family: "Roboto", sans-serif;
  height: 45px;

  display: block;
  width: 100%;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;

  ::placeholder {
    color: #ddd;
  }

  :focus {
    color: #495057;
    background-color: #fff;
    border-color: #80bdff;
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
  }
`;

const Label = styled.label`
  font-size: 16px;
  opacity: 1;
  line-height: 1;
  top: 0;
  margin-bottom: 10px;
`;

const InputWrapper = styled.div`
  font-size: 14px;
  position: relative;
  margin-bottom: 0;
  padding-bottom: 0.5em;
  margin-bottom: 15px;
  color: #333;
`;

const ButtonWrapper = styled.div`
  text-align: center;
  display: block;
  width: 100%;
  margin: 15px 0 0;
`;

const LowerForm = styled.div`
  display: inline-block;
  float: right;
  width: 100%;
  margin: 10px 0 0;
  text-align: right;
`;

const StyledLink = styled(Link)`
  color: #333333;
  transition: all 0.2s;
  :hover {
    color: #17d0cf;
    text-decoration: none;
  }
  cursor: pointer;
`;

const Login = ({ history, isLoading, isError, apiCall, closeErr }) => {
  const [loginForm, setLoginForm] = React.useState({});
  React.useEffect(() => {
    return () => {
      setLoginForm({});
    };
  }, []);
  const handleChange = e => {
    const temp = loginForm;
    temp[e.target.name] = e.target.value;
    setLoginForm(temp);
  };
  const login = async e => {
    e.preventDefault();
    const res = await apiCall({
      method: "post",
      data: loginForm,
      path: "/login"
    });
    if (res) {
      setUserToken(res.token);
      setUserId(res.id);
      history.push(privatePath.dashboard);
    }
  };
  return (
    <Wrapper
      isLoading={isLoading}
      isError={isError}
      closeErr={closeErr}
      errorText="ชื่อผู้ใช้หรือรหัสผ่านไม่ถูกต้อง"
      isPublicPage>
      <div className="col-lg-8 col-md-10 mx-auto">
        <div className="text-center">
          <H3>เข้าสู่ระบบ</H3>
        </div>

        <Form id="login-form" onSubmit={login}>
          <InputWrapper className="form-group floating-label-form-group controls">
            <Label>ชื่อผู้ใช้</Label>
            <Input
              type="text"
              onChange={handleChange}
              className="form-control"
              placeholder="username"
              name="email"
              id="name"
              data-validation-required-message="Please enter your name."
            />
          </InputWrapper>
          <InputWrapper className="form-group floating-label-form-group controls">
            <Label>รหัสผ่าน</Label>
            <Input
              type="password"
              className="form-control"
              onChange={handleChange}
              name="password"
              placeholder="password"
              id="email"
              data-validation-required-message="Please enter your email address."
            />
          </InputWrapper>
          <ButtonWrapper>
            <Btn type="submit" id="login-btn">
              เข้าสู่ระบบ
            </Btn>
            <LowerForm>
              <StyledLink to={publicPath.forgetPassword}>
                ลืมรหัสผ่าน?
              </StyledLink>
            </LowerForm>
          </ButtonWrapper>
        </Form>
      </div>
    </Wrapper>
  );
};

export default withApiWrapper(Login);
