import React from "react";
import styled from "styled-components";

const Wrapper = styled.div`
  margin-bottom: 1rem;
`;

const Label = styled.label`
font-size:16px;
margin-bottom: .5rem;
display-inline-block
color: #333
cursor:default;`;

const RedText = styled.span`
  align-self: center;
  font-size: 12px;
  color: #ff0000;
`;

const Description = styled.small`
  color: ${props => (props.isError ? "#ff0000" : "#6c757d")}!important;
  display: block;
`;

const InputWrapper = ({
  className,
  labelText,
  required,
  children,
  isError,
  errorText,
  description
}) => {
  return (
    <Wrapper className={className}>
      <Label>
        {labelText}
        {required && <RedText>{" *จำเป็น"}</RedText>}
      </Label>
      {children}
      <Description className="form-text text-muted" isError={isError}>
        {(isError && errorText) || description}
      </Description>
    </Wrapper>
  );
};

export default InputWrapper;
