import React from "react";
import styled from "styled-components";
import Wrapper from "../../components/page-wrapper";
import { GradientButton, WhiteButton } from "../../components/button";
import withApiWrapper from "../../utils/withApiWrapper";
import { publicPath } from "../../utils/route-constants";
import Form from "./form";
import formObject from "./form-validator";

const H3 = styled.h3`
  font-weight: 600;
  margin-bottom: 30px;
`;

const ButtonWrapper = styled.div`
  text-align: center;
  display: block;
  width: 100%;
  margin: 30px 0 0;
`;

const P = styled.p`
  margin: 30px 0;
  ${props => props.red && "color: red;"}
`;

const Div = styled.div`
  text-align: center;
  margin: 20px 0 0;
  border-top: 1px solid #ddd;
  padding-top: 20px;
  width: 100%;
`;

const initState = { allowBonus: true };
Object.values(formObject).forEach(val => (initState[val.name] = ""));

const Register = ({ history, isLoading, isError, apiCall, closeErr }) => {
  const [formValue, setFormValue] = React.useState({ ...initState });
  const [error, setError] = React.useState({});
  const [formStep, setformStep] = React.useState(true);
  const [policyErr, setPolicyErr] = React.useState(false);
  const isPolicyAccepted = React.useRef(null);
  React.useState(() => {
    return () => {
      setFormValue({ ...initState });
      setError({});
    };
  });
  const onValChange = e => {
    const temp = formValue;
    temp[e.target.name] = e.target.value;
    setFormValue(temp);
  };

  const handleSubmitForm = () => {
    const temp = {};
    Object.keys(formObject).forEach(val => {
      temp[formObject[val].name] = formObject[val].validator(
        formValue[formObject[val].name]
      );
    });
    setError(temp);
    for (const val of Object.values(temp)) {
      if (val) return;
    }
    setformStep(false);
  };
  const handleRegister = async () => {
    if (!isPolicyAccepted.current.checked) {
      setPolicyErr(true);
      return;
    }
    setPolicyErr(false);
    const response = await apiCall({
      method: "post",
      data: formValue,
      path: "/register"
    });
    if (response) {
      history.push(publicPath.login);
    }
  };
  const handleBack = () => {
    setFormValue({ ...initState });
    setformStep(true);
  };
  return (
    <Wrapper
      isLoading={isLoading}
      isError={isError}
      closeErr={closeErr}
      errorText="เกิดข้อผิดพลาดขึ้น ไม่สามารถใช้งานได้ในขณะนี้&#10;อีเมล์นี้อาจถูกใช้ไปแล้ว"
      isPublicPage>
      <div className="col-md-10 mx-auto register-page">
        <div className="text-center">
          <H3>สมัครสมาชิก</H3>
        </div>
        {formStep ? (
          <Form onValChange={onValChange} errors={error} values={formValue} />
        ) : (
          <div className="row">
            <P>
              คุณต้องการรับโบนัสจากการเติมเงินหรือไม่
              (สามารถเปลี่ยนแปลงได้หลังจากการสมัคร)
            </P>
            <div className="form-group col-12">
              <input
                onChange={onValChange}
                type="radio"
                name="allowBonus"
                checked
                value="true"
              />
              <label for="withbonus">&nbsp;รับโบนัส</label>
              <br />
              <input
                onChange={onValChange}
                type="radio"
                name="allowBonus"
                value="false"
              />
              <label for="withoutbonus">&nbsp;ไม่รับโบนัส</label>
            </div>
            <Div className="form-group col-12">
              <input
                type="checkbox"
                id="terms"
                // checked=""
                ref={isPolicyAccepted}
                name="isPolicyAccepted"
                required=""
              />
              <label for="terms">
                &nbsp;ฉันยอมรับเงื่อนไขและข้อตกลงการใช้งาน
              </label>
              {policyErr && <P red>กรุณายอมรับเงื่อนและข้อตกลงใช้งานก่อน</P>}
            </Div>
          </div>
        )}

        <ButtonWrapper>
          {!formStep && (
            <WhiteButton id="back-btn" onClick={handleBack}>
              กลับ
            </WhiteButton>
          )}
          <GradientButton
            onClick={formStep ? handleSubmitForm : handleRegister}
            id="login-Blue"
            margin="0 0 0 15px">
            {formStep ? "ขั้นตอนต่อไป" : "สมัครสมาชิก"}
          </GradientButton>
        </ButtonWrapper>
      </div>
    </Wrapper>
  );
};

export default withApiWrapper(Register);
