import React from "react";
import styled from "styled-components";
import InputWrapper from "./input-wrapper";

const Input = styled.input`
  ${props => props.isError && "background-color: #ffdddd;"}
`;

const Select = styled.select`
  ${props => props.isError && "background-color: #ffdddd;;"}
`;

export const TextInput = ({
  className,
  type,
  onChange,
  name,
  labelText,
  required,
  isError,
  errorText,
  value,
  description,
  placeholder
}) => {
  return (
    <InputWrapper
      className={className}
      labelText={labelText}
      required={required}
      isError={isError}
      errorText={errorText}
      description={description}>
      <Input
        onChange={onChange}
        isError={isError}
        name={name}
        type={type}
        value={value}
        className="form-control"
        id="text-input"
        aria-describedby="emailHelp"
        placeholder={placeholder}
        format={type === "date" ? "dd/mm/yyyy" : undefined}
      />
    </InputWrapper>
  );
};

export const DropDown = ({
  className,
  onChange,
  name,
  labelText,
  required,
  isError,
  errorText,
  description,
  placeholder,
  value,
  options = []
}) => {
  const list = options.map(({ value, text }) => (
    <option value={value}>{text || value}</option>
  ));
  return (
    <InputWrapper
      className={className}
      name={name}
      labelText={labelText}
      required={required}
      isError={isError}
      errorText={errorText}
      description={description}>
      <Select
        onChange={onChange}
        className="custom-select"
        id="inputGroupSelect"
        isError={isError}
        name={name}
        value={value}>
        <option disabled selected>
          {placeholder}
        </option>
        {list}
      </Select>
    </InputWrapper>
  );
};
