const isNil = val => {
  if (!Boolean(val)) {
    return "จำเป็น";
  }
};

const maxLengthCheck = (val, m, n) => {
  if (val.length > m) return `ความยาวต้องไม่เกิน ${m} ตัว`;
  if (val.length < n) return `ความยาวต้องไม่น้อยกว่า ${n} ตัว`;
};

const numberCheck = val => {
  if (isNaN(val)) return "ต้องเป็นตัวเลขเท่านั้น";
};

// validator return data or true in case error occurs
const formObject = {
  firstName: {
    name: "firstName",
    validator: val => {
      const nil = isNil(val);
      if (nil) return nil;
      const tooLong = maxLengthCheck(val, 30);
      if (tooLong) return tooLong;
    }
  },
  lastName: {
    name: "lastName",
    validator: val => {
      const nil = isNil(val);
      if (nil) return nil;
      const tooLong = maxLengthCheck(val, 30);
      if (tooLong) return tooLong;
    }
  },
  sex: {
    name: "sex",
    validator: isNil,
    options: [
      { value: "MALE", text: "ชาย" },
      { value: "FEMALE", text: "หญิง" }
    ]
  },
  dob: { name: "dateOfBirth", validator: isNil },
  bank: {
    name: "bankName",
    options: [
      { value: "DBBK", text: "ธนาคารดอยซ์แบงก์ (DBBK)" },
      { value: "BBL", text: "ธนาคารกรุงเทพ (BBL)" },
      { value: "KBank", text: "ธนาคารกสิกรไทย (KBank)" },
      { value: "SCB", text: "ธนาคารไทยพาณิชย์ (SCB)" },
      { value: "KTB", text: "ธนาคารกรุงไทย (KTB)" },
      { value: "BAAC", text: "ธนาคารธกส (BAAC)" },
      { value: "TMB", text: "ธนาคารทหารไทย (TMB)" },
      { value: "ICBC", text: "ธนาคารไอซีบีซี (ICBC)" },
      { value: "TCRB", text: "ธนาคารไทยเครดิต (TCRB)" },
      { value: "CITI", text: "ธนาคารซิตี้แบงก์ (CITI)" },
      { value: "SMBC", text: "ธนาคารซูมิโตโม มิตซุย (SMBC)" },
      { value: "SCBT", text: "ธนาคารสแตนดาร์ดชาร์เต (SCBT)" },
      { value: "CIMB", text: "ธนาคารซีไอเอ็มบี (CIMB)" },
      { value: "UOB", text: "ธนาคารยูโอบี (UOB)" },
      { value: "BAY", text: "ธนาคารกรุงศรีฯ (BAY)" },
      { value: "GSB", text: "ธนาคารออมสิน (GSB)" },
      { value: "HSBC", text: "ธนาคารเอชเอสบีซี (HSBC)" },
      { value: "MHCB", text: "ธนาคารมิสซูโฮ (MHCB)" },
      { value: "GHB", text: "ธนาคารธอส. (GHB)" },
      { value: "LHBANK", text: "ธนาคารแลนด์แอนด์เฮ้าส (LHBANK)" },
      { value: "TBNK", text: "ธนาคารธนชาต (TBNK)" },
      { value: "TISCO", text: "ธนาคารทิสโก้ (TISCO)" },
      { value: "KK", text: "ธนาคารเกียรตินาคิน (KK)" },
      { value: "ISBT", text: "ธนาคารอิสลาม (ISBT)" }
    ],
    validator: isNil
  },
  accountNo: {
    name: "bankAccountNumber",
    validator: val => {
      const nil = isNil(val);
      if (nil) return nil;
      const notNumber = numberCheck(val);
      if (notNumber) return notNumber;
      const tooLong = maxLengthCheck(val, 16, 16);
      if (tooLong) return tooLong;
    }
  },
  email: {
    name: "email",
    validator: val => {
      const nil = isNil(val);
      if (nil) return nil;
      const incorrectFormat =
        !/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(
          val
        ) && "รูปแบบไม่ถูกต้อง";
      if (incorrectFormat) return incorrectFormat;
      const tooLong = maxLengthCheck(val, 50);
      if (tooLong) return tooLong;
    }
  },
  password: {
    name: "password",
    validator: val => {
      const nil = isNil(val);
      if (nil) return nil;
      const tooLong = maxLengthCheck(val, 20);
      if (tooLong) return tooLong;
      //   const incorrectFormat = /[0-9]/.test(val) || /[a]
    }
  },
  phoneNo: {
    name: "phoneNumber",
    validator: val => {
      const nil = isNil(val);
      if (nil) return nil;
      const notNumber = numberCheck(val);
      if (notNumber) return notNumber;
      const tooLong = maxLengthCheck(val, 12, 10);
      if (tooLong) return tooLong;
      //   const incorrectFormat = /[0-9]/.test(val) || /[a]
    }
  },
  lineId: {
    name: "lineId",
    validator: val => maxLengthCheck(val, 20)
  },
  howToFind: {
    name: "knowFrom",
    options: [
      { value: "facebook", text: "Facebook" },
      { value: "line", text: "Line" },
      { value: "google", text: "Google" },
      { value: "youtube", text: "Youtube" },
      { value: "instagram", text: "Instagram" },
      { value: "sms", text: "SMS" },
      { value: "friend", text: "เพื่อนแนะนำ" },
      { value: "etc", text: "อื่นๆ" }
    ],
    validator: () => ""
  }
};

export default formObject;
