import React from "react";
import axios from "axios";
import swal from "sweetalert";
import { getUserToken, clearUserToken, clearUserId } from "./session";
import { publicPath } from "./route-constants";

export const baseUrl =
  process.env.NODE_ENV === "production"
    ? "http://localhost:9009/api"
    : "http://localhost:9009/api";

const withApiWrapper = Component => props => {
  const [isLoading, setLoading] = React.useState(false);
  const [isError, setError] = React.useState(false);

  const closeErr = () => {
    setError(false);
  };

  React.useEffect(() => {
    return () => {
      setError(false);
      setLoading(false);
    };
  }, []);

  const apiCall = async ({ method, path, data, headers = {}, customUrl }) => {
    setError(false);
    setLoading(true);
    const response = await axios({
      method,
      url: customUrl || `${baseUrl}${path}`,
      data,
      headers: {
        Authorization: `Bearer ${getUserToken()}`,
        // withCredentials: true,
        ...headers
      }
    })
      .then(res => res.data)
      .catch(({ response: res }) => {
        setError(true);
        if (res.status === 403 || res.status === 401) {
          swal({
            title: "คุณไม่มีสิทธิเข้าใช้งานในขณะนี้",
            text: "กรุณาลงชื่อเข้าใช้ใหม่",
            icon: "error",
            button: "ตกลง",
            dangerMode: true
          }).then(() => {
            clearUserToken();
            clearUserId();
            props.history.push(publicPath.login);
          });
        }
      });
    setLoading(false);
    return response;
  };

  return (
    <Component
      {...props}
      apiCall={apiCall}
      isLoading={isLoading}
      isError={isError}
      closeErr={closeErr}
    />
  );
};

export default withApiWrapper;
