import React from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import qs from "qs";
import Wrapper from "../../components/page-wrapper";
import Btn from "../../components/button/";
import withApiWrapper from "../../utils/withApiWrapper";
import { publicPath } from "../../utils/route-constants";

const H3 = styled.h3`
  font-weight: 600;
  margin-bottom: 30px;
`;

const Form = styled.form`
  padding: 30px;
  display: block;
`;

const Input = styled.input`
  font-size: 14px;
  position: relative;
  z-index: 1;
  padding: 8px 10px;
  resize: none;
  border: 1px solid #ddd;
  border-radius: 0;
  background: none;
  box-shadow: none !important;
  font-family: "Roboto", sans-serif;
  height: 45px;

  display: block;
  width: 100%;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;

  ::placeholder {
    color: #ddd;
  }

  :focus {
    color: #495057;
    background-color: #fff;
    border-color: #80bdff;
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
  }
`;

// const Label = styled.label`
//   font-size: 16px;
//   opacity: 1;
//   line-height: 1;
//   top: 0;
//   margin-bottom: 10px;
// `;

const InputWrapper = styled.div`
  font-size: 14px;
  position: relative;
  margin-bottom: 0;
  padding-bottom: 0.5em;
  margin-bottom: 15px;
  color: #333;
`;

const ButtonWrapper = styled.div`
  text-align: center;
  display: block;
  width: 100%;
  margin: 15px 0 0;
`;

const LowerForm = styled.div`
  display: inline-block;
  float: right;
  width: 100%;
  margin: 10px 0 0;
  text-align: right;
`;

const StyledLink = styled(Link)`
  color: #333333;
  transition: all 0.2s;
  :hover {
    color: #17d0cf;
    text-decoration: none;
  }
  cursor: pointer;
`;

const Small = styled.small`
  color: red;
`;

const CompleteWrapper = styled.div`
  text-align: center;
`;

const ResetPassword = ({
  isLoading,
  isError,
  apiCall,
  closeErr,
  location,
  history
}) => {
  const token = qs.parse(location.search, { ignoreQueryPrefix: true }).token;
  const [password, setPassword] = React.useState("");
  const [error, setError] = React.useState(false);
  const [isComplete, setIsComplete] = React.useState(false);
  React.useEffect(() => {
    if (!token) {
      history.push(publicPath.login);
    }
    return () => {
      setPassword("");
      setError(false);
      setIsComplete(false);
    };
  }, []);
  const handleChange = e => {
    setPassword(e.target.value);
  };
  const handleSubmitResetPassword = async e => {
    e.preventDefault();
    if (password.length < 8) {
      setError("รหัสผ่านต้องมีความยาวกว่า 8 ตัว");
      return;
    }
    if (password.length > 20) {
      setError("รหัสผ่านต้องมีความยาวไม่เกิน 20 ตัว");
      return;
    }
    setError(false);
    const res = await apiCall({
      method: "post",
      data: { password, token },
      path: "/reset-password"
    });
    if (res) {
      setIsComplete(true);
    }
  };
  return (
    <Wrapper
      isLoading={isLoading}
      isError={isError}
      closeErr={closeErr}
      errorText="คำขอเปลี่ยนรหัสอาจไม่ถูกต้องหรือหมดอายุแล้ว กรุณาส่งคำขอเปลี่ยนรหัสใหม่"
      isPublicPage>
      <div className="col-lg-8 col-md-10 mx-auto">
        <div className="text-center">
          <H3>
            {isComplete
              ? "เปลี่ยนพาสเวิร์ดเรียบร้อยแล้ว"
              : "กรุณากรอกรหัสผ่านใหม่"}
          </H3>
        </div>

        {isComplete ? (
          <CompleteWrapper>
            <p>
              พาสเวิร์ดของท่านได้ถูกเปลี่ยนแล้ว
              <br />
              ท่านสามารถเข้าสู่ระบบด้วยพาสเวิร์ดที่ท่านพึ่งตั้งใหม่เมื่อซักครู่นี้
            </p>
            <br />
            <Link to={publicPath.login}>เข้าสู่ระบบ</Link>
          </CompleteWrapper>
        ) : (
          <Form
            id="handleSubmitResetPassword-form"
            onSubmit={handleSubmitResetPassword}>
            <InputWrapper className="form-group floating-label-form-group controls">
              {/* <Label>พาสเวิร์ด</Label> */}
              <Input
                type="password"
                onChange={handleChange}
                className="form-control"
                // placeholder="เช่น example@wallet.com"
                name="email"
                id="name"
                // data-validation-required-message="Please enter your name."
              />
              {error && <Small>{error}</Small>}
            </InputWrapper>
            <ButtonWrapper>
              <Btn type="submit" id="reset-btn">
                บันทึก
              </Btn>
              <LowerForm>
                <StyledLink to={publicPath.login}>เข้าสู่ระบบ</StyledLink>
              </LowerForm>
            </ButtonWrapper>
          </Form>
        )}
      </div>
    </Wrapper>
  );
};

export default withApiWrapper(ResetPassword);
