import React from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";
import Button from "./button";
import { publicPath } from "../../utils/route-constants";

import logo from "../../images/logo.png";

const StyledImg = styled.img`
  width: 45px;
  vertical-align: middle;
  border-style: none;
  cursor: pointer;
`;

const ButtonDiv = styled.div`
  align-self: center;
  text-decoration: none;
  display: flex;
  width: fit-content;
`;

const Wrapper = styled.div`
  display: flex;
  justify-content: space-between;
`;

const StyledLink = styled(Link)`
  :hover {
    text-decoration: none;
  }
`;

const PublicNavbar = ({ match: { url } }) => {
  const isLoginPage = Boolean(String(url).match(`(${publicPath.login})`));
  const isRegPage = Boolean(String(url).match(`(${publicPath.register})`));

  return (
    <Wrapper>
      <StyledImg src={logo} className="img-fluid" />
      <ButtonDiv>
        <StyledLink to={publicPath.register}>
          <Button
            text="สมัครสมาชิก"
            iconClass="fas fa-user"
            invertColor={isRegPage}
          />
        </StyledLink>
        <StyledLink to={publicPath.login}>
          <Button
            text="เข้าสู่ระบบ"
            iconClass="fas fa-sign-in-alt"
            marginLeft={10}
            invertColor={isLoginPage}
          />
        </StyledLink>
      </ButtonDiv>
    </Wrapper>
  );
};

export default PublicNavbar;
