import React from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import Wrapper from "../../components/page-wrapper";
import Btn from "../../components/button/";
import withApiWrapper from "../../utils/withApiWrapper";
import { publicPath } from "../../utils/route-constants";

const H3 = styled.h3`
  font-weight: 600;
  margin-bottom: 30px;
`;

const Form = styled.form`
  padding: 30px;
  display: block;
`;

const Input = styled.input`
  font-size: 14px;
  position: relative;
  z-index: 1;
  padding: 8px 10px;
  resize: none;
  border: 1px solid #ddd;
  border-radius: 0;
  background: none;
  box-shadow: none !important;
  font-family: "Roboto", sans-serif;
  height: 45px;

  display: block;
  width: 100%;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;

  ::placeholder {
    color: #ddd;
  }

  :focus {
    color: #495057;
    background-color: #fff;
    border-color: #80bdff;
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
  }
`;

const Label = styled.label`
  font-size: 16px;
  opacity: 1;
  line-height: 1;
  top: 0;
  margin-bottom: 10px;
`;

const InputWrapper = styled.div`
  font-size: 14px;
  position: relative;
  margin-bottom: 0;
  padding-bottom: 0.5em;
  margin-bottom: 15px;
  color: #333;
`;

const ButtonWrapper = styled.div`
  text-align: center;
  display: block;
  width: 100%;
  margin: 15px 0 0;
`;

const LowerForm = styled.div`
  display: inline-block;
  float: right;
  width: 100%;
  margin: 10px 0 0;
  text-align: right;
`;

const StyledLink = styled(Link)`
  color: #333333;
  transition: all 0.2s;
  :hover {
    color: #17d0cf;
    text-decoration: none;
  }
  cursor: pointer;
`;

const CompleteWrapper = styled.div`
  text-align: center;
`;

const ForgetPassword = ({ isLoading, isError, apiCall, closeErr }) => {
  const [emailPayload, setEmailPayload] = React.useState({});
  const [isComplete, setIsComplete] = React.useState(false);
  React.useEffect(() => {
    return () => {
      setEmailPayload({});
      setIsComplete(false);
    };
  }, []);
  const handleChange = e => {
    const temp = emailPayload;
    temp[e.target.name] = e.target.value;
    setEmailPayload(temp);
  };
  const handleSubmitResetPassword = async e => {
    e.preventDefault();
    const res = await apiCall({
      method: "post",
      data: emailPayload,
      path: "/request-reset-password"
    });
    if (res) {
      setIsComplete(true);
    }
  };
  return (
    <Wrapper
      isLoading={isLoading}
      isError={isError}
      closeErr={closeErr}
      isPublicPage>
      <div className="col-lg-8 col-md-10 mx-auto">
        <div className="text-center">
          <H3>
            {isComplete
              ? "ส่งคำขอรีเซ็ทพาสเวิร์ดเรียบร้อย"
              : "กรุณากรอกอีเมล์ที่ใช้ลงทะเบียน"}
          </H3>
        </div>

        {isComplete ? (
          <CompleteWrapper>
            <p>
              เราส่งลิ้งค์สำหรับรีเซ็ทพาสเวิร์ดของบัญชีผู้ใช้ตามอีเมล์ที่ท่านกรอกมาแล้ว
              <br />
              กรุณาตรวจสอบอีเมล์ของท่าน หากไม่พบกรุณาตรวจสอบในกล่องอีเมล์ขยะ
            </p>
            <br />
            <Link to={publicPath.login}>เข้าสู่ระบบ</Link>
          </CompleteWrapper>
        ) : (
          <Form
            id="handleSubmitResetPassword-form"
            onSubmit={handleSubmitResetPassword}>
            <InputWrapper className="form-group floating-label-form-group controls">
              <Label>อีเมล์</Label>
              <Input
                type="text"
                onChange={handleChange}
                className="form-control"
                placeholder="เช่น example@wallet.com"
                name="email"
                id="name"
                data-validation-required-message="Please enter your name."
              />
            </InputWrapper>
            <ButtonWrapper>
              <Btn type="submit" id="reset-btn">
                ขอรีเซ็ทรหัสผ่าน
              </Btn>
              <LowerForm>
                <StyledLink to={publicPath.login}>เข้าสู่ระบบ</StyledLink>
              </LowerForm>
            </ButtonWrapper>
          </Form>
        )}
      </div>
    </Wrapper>
  );
};

export default withApiWrapper(ForgetPassword);
