import React from "react";
import styled from "styled-components";
import { Switch, Route } from "react-router-dom";
import PublicNavbar from "./publicNavbar";
import PrivateNavbar from "./privateNavbar";
import { publicPath, privatePath } from "../../utils/route-constants";
import { getUserToken } from "../../utils/session";

const StyledNav = styled.nav`
  background-color: #fff;
  padding: 15px;
  border-bottom: 1px solid #e9ecef;
`;

const publicRegex = `(${Object.values(publicPath).join("|")}|/*)`;
const privateRegex = `(${Object.values(privatePath).join("|")})`;

const Navbar = () => {
  return (
    <header>
      <StyledNav id="mainNav">
        <Switch>
          <Route
            path={privateRegex}
            render={props => getUserToken() && <PrivateNavbar {...props} />}
          />
          <Route
            path={publicRegex}
            render={props => <PublicNavbar {...props} />}
          />
        </Switch>
      </StyledNav>
    </header>
  );
};

export default Navbar;
