import React from "react";
import styled from "styled-components";
import Wrapper from "../../components/page-wrapper";
import { GradientButton, WhiteButton } from "../../components/button";
import withApiWrapper from "../../utils/withApiWrapper";
import { getUserId } from "../../utils/session";
import { privatePath } from "../../utils/route-constants";

const Form = styled.form`
  margin: 50px auto 0 auto;
`;

const FormRow = styled.div`
  padding: 0;
  margin-bottom: 25px;
  ${props => props.center && "text-align: center;"}
`;

const Label = styled.label`
  padding: 0;
  text-align: right;
  width: 140px;
`;

const Small = styled.small`
  color: red;
`;

const Setting = ({ history, isLoading, isError, apiCall, closeErr }) => {
  const [formValue, setFormValue] = React.useState({});
  const [error, setError] = React.useState({});

  const handleChange = e => {
    const temp = formValue;
    temp[e.target.name] = e.target.value;
    setFormValue(temp);
  };

  React.useState(() => {
    return () => {
      setFormValue({});
    };
  });

  const handleSubmit = async e => {
    e.preventDefault();
    const temp = {};
    if (formValue.password) {
      if (!formValue.oldPassword) {
        temp.oldPassword = "กรุณากรอกรหัสผ่านเก่า";
        setError(temp);
        return;
      }
      if (formValue.password.length < 8) {
        temp.password = "รหัสผ่านต้องมีความยาวกว่า 8 ตัว";
        setError(temp);
        return;
      }
      if (formValue.password.length > 20) {
        temp.password = "รหัสผ่านต้องมีความยาวไม่เกิน 20 ตัว";
        setError(temp);
        return;
      }
    }
    setError(temp);
    const payload = {};
    Object.keys(formValue).forEach(name => {
      if (formValue[name]) {
        payload[name] = formValue[name];
      }
    });
    const res = await apiCall({
      method: "put",
      data: payload,
      path: `/wallet-owner/${getUserId()}`
    });
    if (res) {
      history.push(privatePath.dashboard);
    }
  };

  return (
    <Wrapper
      defaultTitle="ตั้งค่าบัญชี"
      errorText="เกิดข้อผิดพลาดขึ้น ไม่สามารถใช้งานได้ในขณะนี้ รหัสผ่านเก่าอาจไม่ถูกต้อง"
      isLoading={isLoading}
      isError={isError}
      closeErr={closeErr}>
      <div className="col-12">
        <Form
          className="col-12 col-md-8 col-lg-6 mx-auto"
          onSubmit={handleSubmit}>
          <FormRow className="form-group row">
            <Label className="colm-5 col-form-label">ตั้งค่าการรับโบนัส</Label>
            <div className="col-7">
              <div className="form-check">
                <input
                  onChange={handleChange}
                  className="form-check-input"
                  type="radio"
                  name="allowBonus"
                  id="gridRadios1"
                  value="true"
                />
                <label className="form-check-label" htmlFor="gridRadios1">
                  รับโบนัส
                </label>
              </div>
              <div className="form-check">
                <input
                  onChange={handleChange}
                  className="form-check-input"
                  type="radio"
                  name="allowBonus"
                  id="gridRadios2"
                  value="false"
                />
                <label className="form-check-label" htmlFor="gridRadios2">
                  ไม่รับโบนัส
                </label>
              </div>
            </div>
          </FormRow>

          <FormRow className="form-group row">
            <Label htmlFor="inputOldPassword" className="colm-5 col-form-label">
              เปลี่ยนรหัสผ่าน
            </Label>
            <div className="col-7">
              <input
                onChange={handleChange}
                type="password"
                name="oldPassword"
                className="form-control"
                id="inputOldPassword"
                placeholder="กรอกรหัสผ่านเก่า"
              />
              {error.oldPassword && <Small>{error.oldPassword}</Small>}
            </div>
          </FormRow>
          <FormRow className="form-group row">
            <Label
              htmlFor="inputPassword"
              className="colm-5 col-form-label"></Label>
            <div className="col-7">
              <input
                onChange={handleChange}
                type="password"
                name="password"
                className="form-control"
                id="inputPassword"
                placeholder="กรอกรหัสผ่านใหม่"
              />
              {error.password && <Small>{error.password}</Small>}
            </div>
          </FormRow>
          <FormRow center>
            <WhiteButton
              id="back-btn"
              onClick={() => {
                history.push(privatePath.dashboard);
              }}>
              ยกเลิก
            </WhiteButton>
            <GradientButton
              // onClick={formStep ? handleSubmitForm : handleRegister}
              type="submit"
              id="login-Blue"
              margin="0 0 0 15px">
              บันทึก
            </GradientButton>
          </FormRow>
        </Form>
      </div>
    </Wrapper>
  );
};

export default withApiWrapper(Setting);
